import React, { Component } from 'react';
import styles from'./PetitionGenerator.module.css';

import PetitionTemplate from './PetitionTemplate';

function InputRow (props){
    return(
        <div className={styles.formRow}>
            <label >
                <h3>{props.title}</h3>
                <p>{props.subTitle}</p>
                <input
                    name = {props.inputName}                                  
                    type = "text"
                    placeholder = {props.placeholder}
                    value = {props.value}
                    onChange = {props.onChange}
                />
                {props.NameError ? <div className={styles.errorMessage}>{props.NameError}</div>: null}
            </label>
        </div>
    )
}

const initialState = {
    name: '',
    address: '',
    courtName: '',
    caseNumber: '',

    nameError: '',
    addressError: '',
    courtNameError: '',
    caseNumberError: '',
    submitted: false,
}

class PetitionGenerator extends Component{
    constructor(props) {
        super(props);
        this.state = initialState;

        this.handleChange = this.handleChange.bind(this);
        this.handleSubmit = this.handleSubmit.bind(this);
    }

    validate(){
        let nameError = '';
        let addressError = '';
        let courtNameError = '';
        let caseNumberError = '';

        if (!this.state.name ){
            nameError = 'Укажите имя'
        }

        if (!this.state.address){
            addressError = 'Укажите адрес'
        //    this.setState({formErrors: {address:'Укажите адрес'}});
        }

        if (!this.state.courtName ){
            courtNameError = 'Укажите номер и адрес участка'
        }

        if (!this.state.caseNumber ){
            caseNumberError = 'Укажите статью и часть КоАП РФ'
        }

        if (nameError || addressError || courtNameError || caseNumberError){
            this.setState({nameError, addressError, courtNameError, caseNumberError});
            return false
        }

        return true;

    }

    handleSubmit(event){
        event.preventDefault();
        const isValid = this.validate();
        if (isValid) {
//            console.log('Поля формы заполнены', this.state);

            //clear form
//            this.setState(initialState);
            this.setState({ submitted: true })
        }
//        console.log('Ошибка ', this.state);

    }

    handleChange(event){
        this.setState({
            [event.target.name]: event.target.value});
    }

    render() {
        
        if(this.state.submitted){
            return(
                <div>
                    <PetitionTemplate 
                        courtName = {this.state.courtName}
                        name = {this.state.name}
                        address = {this.state.address}
                        caseNumber = {this.state.caseNumber}
                    />
                </div>
            );
            
        };
        return (
            <div className={styles.center}>
                <h2>Ходатайство о рассмотрении дела <br></br>
                    об административном правонарушении<br></br>
                    по месту жительства
                </h2>

                <p style={{fontSize:"17px", marginBottom:"30px"}}>Для создания ходатайства заполните поля формы ниже.</p>
                
                <form onSubmit={this.handleSubmit}>

                    <InputRow
                        title = "Как вас зовут"
                        subTitle = "Укажите ваше ФИО в родительном падеже"
                        placeholder = "Иванова Ивана Ивановича"
                        inputName = "name"
                        onChange = {this.handleChange}
                        value = {this.state.name}    
                        NameError = {this.state.nameError}
                    />

                    <InputRow
                        title = "Ваш адрес?"
                        subTitle = "Укажите адрес прописки или регистрации"
                        placeholder = "г. Москва, ул. Тверская д. 1"
                        inputName = "address"
                        onChange = {this.handleChange}
                        value = {this.state.address}    
                        NameError = {this.state.addressError}
                    />

                    <InputRow
                        title = "Номер и адрес судебного участка"
                        subTitle = "Укажите номер, а после адрес судебного участка"
                        placeholder = "№1 Левобережного района г. Москвы"
                        inputName = "courtName"
                        onChange = {this.handleChange}
                        value = {this.state.courtName}    
                        NameError = {this.state.courtNameError}
                    />

                    <InputRow
                        title = "Статьи КоАП РФ по которым идет рассмотрение"
                        subTitle = "Укажите сначала часть, а после номер статьи"
                        placeholder = "ч.1 ст. 29.5"
                        inputName = "caseNumber"
                        onChange = {this.handleChange}
                        value = {this.state.caseNumber}    
                        NameError = {this.state.caseNumberError}
                    />

                    <button className={styles.button} disabled={!this.state.name || !this.state.address || !this.state.courtName ||!this.state.caseNumber }>Создать</button>

                </form>

                <p style={{color:"grey", marginBottom:"0px"}}>
                    PS. Мы не сохраняем ваши данные
                </p>

            </div> 
        );
    }
}
export default PetitionGenerator;